/* Basic Reset and Fonts */
body, html {
  height: 100%; /* Ensures the minimum height of the page is the viewport height */
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  background: linear-gradient(#141e30, #243b55) no-repeat center center fixed; /* Fixes background to not repeat and cover the entire page */
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover; /* Ensures background covers the entire page */
}

.container {
  margin: 20px; /* Adds margin around the entire container */
  padding: 20px; /* Adds padding inside the container */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; /* Align content to the top */
  width: 100%; /* Full width */
  min-height: 100vh; /* Ensures container takes at least the height of the viewport */
}

h1 {
  color: rgb(252, 232, 17); /* h1 color */
  margin-top: 0; /* Align content to top */
}

.tabs button {
  /* Add any required button styling here, for example: */
  background-color: #4675a4; /* Inactive tab */
  color: #f8f9fa;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  font-weight: 600;
  margin-right: 5px;
  border-top-left-radius: 10px; 
  border-top-right-radius: 25px;
}

.tabs button:focus, .tabs button:hover {
  /* Style for hover and focus states */
  background-color: #1a255f;
}

.tabs button.active {
  /* Style for the active tab */
  background-color: #024b18; /* Change to the desired color for the active tab */
}

.tab-content { 
  background-color: #2e2b0b;
  color: #f0f2fc;
  padding: 20px;
  margin-top: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.2);
}

/* Adjustments for Responsiveness */
@media (max-width: 768px) {
  .tabs button {
      padding: 5px 10px;
      font-size: 12px;
  }
}
